@import "https://fonts.googleapis.com/css2?family=Indie+Flower&family=Raleway:wght@300&family=Roboto&display=swap";
body {
  font-family: Roboto, sans-serif;
}

.navbar {
  color: #eef1e6;
  background-color: #150050;
  padding: 1rem;
}

.navbar ul {
  justify-content: space-around;
  list-style: none;
  display: flex;
}

.navbar ul li {
  margin: 0 1rem;
}

.navbar ul li:hover {
  color: #c2cca5;
  cursor: pointer;
}

@media (width <= 760px) {
  .navbar ul {
    flex-direction: column;
  }
}

.color-box {
  color: #4095bf;
  text-align: center;
  background: #0072af;
  border-radius: 8px;
  margin: 10px;
  padding: 20px;
}

.lighten-color {
  background: #8cbfd9;
}

.darken-color {
  background: #265973;
}

.adjust-color {
  background: #ff95bf;
}

.scale-color {
  background: #fff;
}

.change-color {
  background: #6395bf;
}

.mix-color {
  background: #078215;
}

.complement-color {
  background: #bf6a40;
}

.grayscale-color {
  background: gray;
}

.opacify-color {
  background: #4095bf;
}

.invert-color {
  background: #689;
}

.adjust-hue {
  background: #a640bf;
}

.hero {
  color: #3f0071;
  text-align: center;
  background-color: #f5f3f5;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 49vh;
  padding: 50px;
  display: flex;
}

.button-secondary, .button-primary {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: .5rem;
  font-weight: bold;
  transition: background-color .3s;
}

.button-primary {
  color: #333;
  background-color: #582707;
}

.button-primary:hover {
  background-color: #000;
}

.button-secondary {
  color: #333;
  background-color: #c88304;
  margin-top: 10px;
}

.button-secondary:hover {
  background-color: #d8dfc5;
}

.card {
  color: #333;
  background-color: #c88304;
  padding: 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}
